* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: whitesmoke;
    font-family: 'Poppins', sans-serif;
}

.main-container {
    width: 1110px;
    margin: 50px auto 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.search-container {
    width: 400px;
    min-height: 610px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 1px 1px 1px whitesmoke;
    padding: 10px;
    border: 1px solid #ff963381;
}

button {
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
}

.selected-track-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.waiting-heading {
    margin-top: 100px;
}
